.progress-bar {
    color: #3C6B50;
}

.create-option {
    display: flex;
    flex-direction: column;
    width: 100%;
}

.create-option .title {
    width: 100%;
    margin-bottom: 20px;
}

.create-option .description {
    width: 100%;
}

.tag-box {

}

.tag-box input[type="checkbox"] {
    display: none;
}

.tag-box .tag {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    padding: 0px 20px;
    height: 40px;
    background: #F4F4F4;
    border-radius: 40px;
    color: #3C6B50;
    cursor: pointer;

    margin-right: 10px;
    margin-top: 10px;
}

.tag-box .style {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    padding: 0px 20px;
    height: 40px;
    background: #F4F4F4;
    border-radius: 40px;
    color: #3C6B50;
    cursor: pointer;

    margin-right: 10px;
    margin-top: 10px;
}

.tag-box p {
    margin: 0;
    font-family: 'Spoqa Han Sans Neo';
    font-weight: 400;
    font-size: 20px;
    line-height: 40px;
    letter-spacing: -0.05em;
    color: #9F9F9F;
}

.tag-box hr {
    margin: 10px 0;
}

.tag-box input[type="checkbox"]:checked + .tag {
    color: #FFFFFF;
    background: #3C6B50;
}

.tag-box input[type="checkbox"]:disabled + .style {
    background: #717171;
    color: #FFFFFF;
}

.ethereum-wallet-box {
    display: flex;
    flex-wrap: wrap;
    gap: 10px;
}

.ethereum-wallet-box .question-button {
    display: flex;
    border-radius: 50%;
    width: 40px;
    height: 40px;

    padding: 10px 7px 8px 9px;
    background: #FAFAFA;
    box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25);
    align-items: center;
    justify-content: center;
    cursor: pointer;
}

.ethereum-wallet-box .question-button:hover {
    -webkit-filter: brightness(95%);
    filter: brightness(95%);
    -webkit-transition: all 250ms ease;
    -moz-transition: all 250ms ease;
    -o-transition: all 250ms ease;
    -ms-transition: all 250ms ease;
    transition: all 250ms ease;
}

.ethereum-wallet-box .question-button p {
    margin: 0;
    font-family: 'Alata';
    font-weight: 800;
    font-size: 24px;

    color: #101112;
}

.ethereum-wallet-box .ethereum-wallet-address {
    background: #F4F4F4;
    border-radius: 8px;
    border: 0;
    flex-grow: 1;
    height: 40px;
    display: flex;
}

.ethereum-wallet-box .ethereum-wallet-input {
    background: #F4F4F4;
    border-radius: 8px;
    border: 0;
    padding: 0 10px;
    display: inline-flex;
    flex-grow: 1;
    height: 40px;
}

.ethereum-wallet-box .ethereum-wallet-button {
    border: 0;
    display: inline-flex;
    height: 100%;
    padding: 5px;
    justify-self: flex-end;
    align-items: center;
}

.ethereum-wallet-box .ethereum-wallet-button:hover {
    -webkit-filter: brightness(85%);
    filter: brightness(85%);
    -webkit-transition: all 250ms ease;
    -moz-transition: all 250ms ease;
    -o-transition: all 250ms ease;
    -ms-transition: all 250ms ease;
    transition: all 250ms ease;
}

@keyframes rotate {
    from {
        -webkit-transform: rotate(0deg);
        -o-transform: rotate(0deg);
        transform: rotate(0deg);
    }
    to {
        -webkit-transform: rotate(360deg);
        -o-transform: rotate(360deg);
        transform: rotate(360deg);
    }
}

.ethereum-wallet-box .ethereum-wallet-button[loading=true] {
    animation: rotate 1.25s infinite;
}

.ethereum-wallet-box .ethereum-wallet-button img {
    height: 100%;
}

.ethereum-wallet-box .ethereum-wallet-input::placeholder {
    font-family: 'Spoqa Han Sans Neo';
    font-weight: 400;
    font-size: 16px;
    color: #9F9F9F;
}

.button-box {
    display: flex;
    flex-wrap: wrap;
}

.button-box button {
    padding: 0;
    border: none;
    background-color: rgb(0, 0, 0, 0);
    display: flex;
    margin-right: 10px;
}

.button-box .post-button {
    height: 60px;
    padding: 0px 36px;
    flex-direction: row;
    justify-content: center;
    align-items: center;
    background-color: #FF7A00;
    border-radius: 20px;
    flex-grow: 1;
    margin-right: 0;

    font-family: 'Spoqa Han Sans Neo';
    font-style: normal;
    font-weight: 400;
    font-size: 20px;
    line-height: 40px;
    color: #FFFFFF;
}

.button-box .post-button:disabled {
    opacity: 0.5;
}

.tooltip {
    display:flex;
}